import React, { ChangeEvent, KeyboardEvent, useState } from 'react'
import Greeting from './Greeting'
import { UserType } from './HW3'
import { error } from 'console'

type GreetingContainerPropsType = {
    users: UserType[] // need to fix any
    addUserCallback: (name:string)=>void // need to fix any
}

export const pureAddUser = (name: string, setError:  any, setName: any, addUserCallback:(name: string) => void) => {

  if(!name.trim()){
    setError("!  !");
  }else{
    addUserCallback(name);
    setName('');
    setError('');
  }
   //    -  ,  -     
}

export const pureOnBlur = (name:string, setError: any) => { 
    if(!name.trim()){
        setError("!  !");
      }//    -  
}

export const pureOnEnter = (e:React.KeyboardEvent<HTMLInputElement>, addUser: any) => {
    if(e.key==='Enter'){
        addUser()
    } //    Enter - 
}

//      
// function GreetingContainer(props: GreetingPropsType) {

//       :)
const GreetingContainer: React.FC<GreetingContainerPropsType> = ({
    users,
    addUserCallback,
}) => {
    //  
    const [name, setName] = useState<string>('') // need to fix any
    const [error, setError] = useState<string>('') // need to fix any

    const setNameCallback = (e:React.ChangeEvent<HTMLInputElement>) => { // need to fix any
        setName(e.target.value) // need to fix

        error && setError('')
    }
    const addUser = () => {
        pureAddUser(name, setError, setName, addUserCallback)
    }

    const onBlur = () => {
        pureOnBlur(name, setError)
    }

    const onEnter = (e: any) => {
        pureOnEnter(e, addUser)
    }

    const totalUsers =users.length // need to fix
    const lastUserName = users.length ? users[users.length-1].name : "" // need to fix

    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            onBlur={onBlur}
            onEnter={onEnter}
            error={error}
            totalUsers={totalUsers}
            lastUserName={lastUserName}
        />
    )
}

export default GreetingContainer
